<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom:20px" v-show="!dialogFormVisible">
            <el-input class="filter-item" style="width: 300px;" placeholder="文章标题、关键字" v-model="listQuery.title">
            </el-input>
            <span>帮助类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.categoryId" placeholder="全部类型">
                <el-option v-for="status in allListOptions" :key="status.id" :label="status.name" :value="status.id">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">
                查询
            </el-button>
            <el-button style="margin-left: 10px;" type="success" size="small" @click="submit(false)">添加帮助
            </el-button>
        </div>
        <div v-show="!dialogFormVisible">
            <el-table ref="singleTable" :data="tableData" v-loading="listLoading" element-loading-text="给我一点时间" style="width: 100%; ">
                <el-table-column v-for="item in tableHeader" :type="item.type" :key="item.prop" :label="item.label" :prop="item.prop" :width="item.width"
                    :min-width="item.minWidth" :align="item.align" :formatter="item.formatter" show-overflow-tooltip>
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="120">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;height: 30px;margin: 0px">
                            <li style="width: 35px;float: left">
                                <el-button type="text" @click="submit(true,scope.row.id)">修改
                                </el-button>
                            </li>
                            <li style="width: 35px;float: left">
                                <el-button type="text" @click="deleteArticles(scope.row.id)">删除
                                </el-button>
                            </li>
                            <li style="width: 35px;float: left" v-if="scope.row.status===1">
                                <el-button type="text" @click="enableDisable(1 , scope.row.id,2,'隐藏')">隐藏
                                </el-button>
                            </li>
                            <li style="width: 35px;float: left" v-else-if="scope.row.status===2">
                                <el-button type="text" @click="enableDisable(1 , scope.row.id,1,'显示')">显示
                                </el-button>
                            </li>


                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage" :current-page="listQuery.page.page" :page-sizes="[10,20,30, 50]"
                    :page-size="listQuery.page.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>

        <div class="m-addArticle" v-show="dialogFormVisible">


            <div class="m-addArticle-hd">
                <span :class="{ active : addArticleType == 1 }" @click=" addArticleType = 1 ">中文文章</span>
                <span :class="{ active : addArticleType == 2 }" @click=" addArticleType = 2 ">英文文章</span>
            </div>


            <span class="m-addArticle-label">文章类型</span>
            <el-radio-group v-model="postForm.categoryId">
                <el-radio :label="status.id" v-for="(status,index) in userLIstOptions " :key="index">{{ status.name }}</el-radio>
            </el-radio-group>

            <div class="m-addArticle-cn" v-show=" addArticleType == 1 ">
                <div class="m-addArticle-row">
                    <span class="m-addArticle-label">中文标题</span>
                    <el-input v-model="postForm.title"></el-input>
                </div>
                <div class="m-addArticle-row">
                    <span class="m-addArticle-label">中文内容</span>
                    <ue-editor :defaultMsg="defaultContent" @content='editorValue'></ue-editor>
                </div>
            </div>

            <div class="m-addArticle-en" v-show=" addArticleType == 2 ">
                <div class="m-addArticle-row">
                    <span class="m-addArticle-label">英文标题</span>
                    <el-input v-model="postForm.titleEn"></el-input>
                </div>
                <div class="m-addArticle-row">
                    <span class="m-addArticle-label">英文内容</span>
                    <ue-editor :defaultMsg="defaultContentEn" @content='editorValueEn'></ue-editor>  
                </div>
            </div>

            <div class="m-addArticle-ft">
                <el-button @click="dialogFormVisible=false">取消</el-button>
                <el-button type="primary" @click="submitForm">提交</el-button>
            </div>

        </div>

    </div>
</template>
<script>
    // import ArticleDetail from './components/ArticleDetail'
    import DCTP from '@/utils/dctp';
    import Tinymce from '@/components/Tinymce'
    //import Upload from '@/components/Upload/singleImage3'
    import MDinput from '@/components/MDinput'
    import Multiselect from 'vue-multiselect' // 使用的一个多选框组件，element-ui的select不能满足所有需求
    import 'vue-multiselect/dist/vue-multiselect.min.css' // 多选框组件css
    import Sticky from '@/components/Sticky' // 粘性header组件
    import UeEditor from '@/components/editor' // 引入百度富文本
    import {
        validateURL
    } from '@/utils/validate'
    import editorImage from '@/components/Tinymce/components/editorImage'
    import {
        userSearch,
        submitform,
        getarticleall,
        fetchArticle,
        submitalter,
        deletearticles,
        enabledisable
    } from '@/api/article'

    const listOptions = {
        id: 0,
        name: '全部类型',
        nameEn: ""
    };
    const defaultForm = {
        id: '',
        title: '', // 文章题目
        titleEn: '', //英文题目
        content: '', // 文章内容
        contentEn: '', // 英文文章内容
        categoryId: '', //类型
        type: 1, //类目--1代表帮助
        status: 1 //显示设置--1代表显示文章
    };
    export default {
        components: {
            editorImage
        },
        name: 'articleDetail',
        components: {
            Tinymce,
            MDinput,
            Multiselect,
            Sticky,
            UeEditor
        },
        props: {
            isEdit: {
                type: Boolean,
                default: false
            }
        },
        data() {
            const validateRequire = (rule, value, callback) => {
                if (value === '') {
                    this.$message({
                        message: rule.field + '为必传项',
                        type: 'error'
                    })
                    callback(null)
                } else {
                    callback()
                }
            }
            const validateSourceUri = (rule, value, callback) => {
                if (value) {
                    if (validateURL(value)) {
                        callback()
                    } else {
                        this.$message({
                            message: '外链url填写不正确',
                            type: 'error'
                        })
                        callback(null)
                    }
                } else {
                    callback()
                }
            }
            return {
                defaultContentEn: '',
                defaultContent: '',
                addArticleType: 1, //文章类型（1：中午文章 2：英文文章）


                listOptions,
                allListOptions: [],
                dialogFormVisible: null,
                sublime: false,
                category: {
                    type: 1,
                    pageNum: 1,
                    pageSize: 100,
                },
                postForm: Object.assign({}, defaultForm),
                fetchSuccess: true,
                loading: false,
                userLIstOptions: [],
                platformsOptions: [{
                        key: 'a-platform',
                        name: 'a-platform'
                    },
                    {
                        key: 'b-platform',
                        name: 'b-platform'
                    },
                    {
                        key: 'c-platform',
                        name: 'c-platform'
                    }
                ],
                rules: {
                    image_uri: [{
                        validator: validateRequire
                    }],
                    title: [{
                        validator: validateRequire
                    }],
                    titleEn: [{
                        validator: validateRequire
                    }],
                    source_uri: [{
                        validator: validateSourceUri,
                        trigger: 'blur'
                    }]
                },
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                listQuery: {
                    type: 1,
                    categoryId: 0,
                    title: '',
                    page: {
                        page: 1,
                        pageSize: 10
                    }
                },
                tableHeader: [{
                    label: "类型",
                    type: "index",
                    width: 130
                }, {
                    label: "类型",
                    prop: "category",
                    width: 130
                }, {
                    label: "中文标题",
                    prop: "title",
                    width: 300
                }, {
                    label: "英文标题",
                    prop: "titleEn",
                    width: 300
                }, {
                    label: "是否显示",
                    prop: "status",
                    width: 150,
                    align: 'center',
                    formatter(row, column, value) {
                        return value === 1 ? '显示' : (value === 2 ? '隐藏' : '未知');
                    }

                }, {
                    label: "创建时间",
                    prop: "createTime",
                    width: 230,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }, {
                    label: "创建人",
                    prop: "createBy",
                    width: 220,
                }]
            }

        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },
        created() {
            if (this.isEdit) {
                this.fetchData()
            } else {
                this.postForm = Object.assign({}, defaultForm)
                this.defaultContentEn = ''
                this.defaultContent = ''
            }
        },
        methods: {
            /**
             *文本编辑器
             *@param {String}content
             */
            editorValue(content) {
                this.postForm.content = content
            },
            editorValueEn(content) {
                this.postForm.contentEn = content
            },


            onEditorBlur(quill) {
                // console.log('editor blur!', quill)
            },
            onEditorFocus(quill) {
                // console.log('editor focus!', quill)
            },
            onEditorReady(quill) {
                // console.log('editor ready!', quill)
            },
            onEditorChange({
                quill,
                html,
                text
            }) {
                // console.log('editor change!', quill, html, text)
                // this.postForm.content = html
                // console.log( html )
            },
            onEnEditorChange({
                quill,
                html,
                text
            }) {
                // this.postForm.contentEn = html
                // console.log( html )
            },


            //添加或者修改文章
            submit(amend, num) {
                if (amend) {
                    this.fetchData(num);
                    this.sublime = true;
                } else {
                    this.postForm = Object.assign({}, defaultForm)
                    this.sublime = false;
                }
                this.dialogFormVisible = true;
            },
            //获取单个文章列表
            fetchData(num) {
                fetchArticle(num).then(response => {
                    if (response.result) {
                        this.postForm = response.data
                        this.defaultContentEn = response.data.contentEn
                        this.defaultContent = response.data.content
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                })
                console.log(this.postForm);

            },
            //获取所有列表
            getList() {
                this.listLoading = true;
                getarticleall(this.listQuery).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.page.pageSize = val;
                this.getList();
            },

            submitForm() {
                console.log(this.sublime)
                console.log("----------------上是-判断--下是对象---------------------------")
                console.log(this.postForm)
                if (!this.sublime) {
                    submitform(this.postForm).then(response => {
                        if (response.result) {
                            this.loading = true
                            this.$notify({
                                title: '成功',
                                message: '发布文章成功',
                                type: 'success',
                                duration: 2000
                            })
                            this.dialogFormVisible = false
                            this.getList();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'error',
                                message: this.msg
                            });
                            return false
                        }
                    })
                } else {
                    submitalter(this.postForm).then(response => {
                        if (response.result) {
                            this.loading = true
                            this.$notify({
                                title: '修改',
                                message: '修改文章成功',
                                type: 'success',
                                duration: 2000
                            })
                            this.dialogFormVisible = false
                            this.getList();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'error',
                                message: this.msg
                            });
                            return false
                        }
                    })
                }
            },
            draftForm() {
                if (this.postForm.content.length === 0 || this.postForm.title.length === 0) {
                    this.$message({
                        message: '请填写必要的标题和内容',
                        type: 'warning'
                    })
                    return
                }
                this.$message({
                    message: '保存成功',
                    type: 'success',
                    showClose: true,
                    duration: 1000
                })
                this.postForm.status = 'draft'
            },
            getRemoteUserList() {
                userSearch(this.category).then(response => {
                    this.allListOptions = response.data.data;
                    this.userLIstOptions = response.data.data;
                    this.allListOptions.unshift(this.listOptions);
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            },
            deleteArticles(id) {
                deletearticles(id).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    } else {
                        this.msg = '删除帮助成功';
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    }
                })
            },
            enableDisable(type, id, status, str) {
                enabledisable(type, id, status).then(response => {
                    if (response.result) {
                        this.getList();
                    }
                    this.getMessage(response.msg, response.result);
                })
            }
        },
        created() {
            this.getRemoteUserList();
            this.getList();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";

    .title-prompt {
        position: absolute;
        right: 0px;
        font-size: 12px;
        top: 10px;
        color: #ff4949;
    }

    .createPost-container {
        position: relative;
        .createPost-main-container {
            padding: 40px 45px 20px 50px;
            .postInfo-container {
                position: relative;
                @include clearfix;
                margin-bottom: 10px;
                .postInfo-container-item {
                    float: left;
                }
            }
            .editor-container {
                min-height: 500px;
                margin: 0 0 30px;
                .editor-upload-btn-container {
                    text-align: right;
                    margin-right: 10px;
                    .editor-upload-btn {
                        display: inline-block;
                    }
                }
            }
        }
        .word-counter {
            width: 40px;
            position: absolute;
            right: -10px;
            top: 0px;
        }
    }


    .m-addArticle {
        max-width: 1000px;
        .m-addArticle-hd {
            margin-bottom: 30px;
            font-size: 14px;
            border-bottom: 1px solid #e4e7ed;
            span {
                display: inline-block;
                line-height: 40px;
                padding-right: 30px;
                transition: all .3;
                &.active {
                    font-weight: bold;
                    color: #409eff;
                }
                &:hover {
                    cursor: pointer;
                    color: #409eff;
                }
            }
        }
        .m-addArticle-ft {
            margin-top: 70px;
            text-align: right;
        }
        .m-addArticle-label {
            display: block;
            color: #ccc;
            margin-bottom: 10px;
        }
        .el-radio-group,
        .el-input {
            margin-bottom: 40px;
        }
    }
</style>


