<template>
  <div>
    <script :id="editorId" type="text/plain"></script>
    <el-dialog :visible.sync="toUpLoad">
      <div class="">
        <el-upload class="upload-demo" drag action="" :http-request="upLoadImg">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过10M</div>
        </el-upload>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="toUpLoad = false, imgs = []">取 消</el-button>
        <el-button type="primary" @click="toUpLoad = false, recieveImg()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- <img-store ref="upload" :bar="false" @checkedImgs="recieveImg"></img-store> -->
  </div>
</template>
<script>
  // import '../../../static/UE/ueditor.config.js'
  // import '../../../static/UE/ueditor.all.js'
  // import '../../../static/UE/lang/zh-cn/zh-cn.js'
  // import '../../../static/UE/ueditor.parse.min.js'
  // import fetch from '@/utils/fetch';
  import axios from 'axios'
  export default {
    name: 'MyEditor',
    components: {
      //   ImgStore
    },
    data() {
      return {
        editorId: 'editorId_' + Math.random() * Date.now(), // 确保id 唯一
        editor: null,
        imgs: [],
        toUpLoad: false,

        config: {
          // initialFrameWidth: 960,
          initialFrameHeight: 650,
          wordCount: false, // 关闭字数统计
          // elementPathEnabled: false, // 关闭元素路径统计
          autoHeightEnabled: false, // 开启滚动条
          enableAutoSave: false,
          toolbars: [
            [
              'anchor', //锚点
              'undo', //撤销
              'redo', //重做
              'bold', //加粗
              'indent', //首行缩进
              'snapscreen', //截图
              'italic', //斜体
              'underline', //下划线
              'strikethrough', //删除线
              'subscript', //下标
              'fontborder', //字符边框
              'superscript', //上标
              'formatmatch', //格式刷
              'source', //源代码
              'blockquote', //引用
              'pasteplain', //纯文本粘贴模式
              'selectall', //全选
              'print', //打印
              'preview', //预览
              'horizontal', //分隔线
              'removeformat', //清除格式
              'time', //时间
              'date', //日期
              'unlink', //取消链接
              'insertrow', //前插入行
              'insertcol', //前插入列
              'mergeright', //右合并单元格
              'mergedown', //下合并单元格
              'deleterow', //删除行
              'deletecol', //删除列
              'splittorows', //拆分成行
              'splittocols', //拆分成列
              'splittocells', //完全拆分单元格
              'deletecaption', //删除表格标题
              'inserttitle', //插入标题
              'mergecells', //合并多个单元格
              'deletetable', //删除表格
              'cleardoc', //清空文档
              'insertparagraphbeforetable', //"表格前插入行"
              'insertcode', //代码语言
              'fontfamily', //字体
              'fontsize', //字号
              'paragraph', //段落格式
              'imagedes', // 图片上传
              'edittable', //表格属性
              'edittd', //单元格属性
              'link', //超链接
              'emotion', //表情
              'spechars', //特殊字符
              'searchreplace', //查询替换
              'map', //Baidu地图
              'gmap', //Google地图
              'help', //帮助
              'justifyleft', //居左对齐
              'justifyright', //居右对齐
              'justifycenter', //居中对齐
              'justifyjustify', //两端对齐
              'forecolor', //字体颜色
              'backcolor', //背景色
              'insertorderedlist', //有序列表
              'insertunorderedlist', //无序列表
              'fullscreen', //全屏
              'directionalityltr', //从左向右输入
              'directionalityrtl', //从右向左输入
              'rowspacingtop', //段前距
              'rowspacingbottom', //段后距
              'pagebreak', //分页
              'insertframe', //插入Iframe
              'imagenone', //默认
              'imageleft', //左浮动
              'imageright', //右浮动
              'imagecenter', //居中
              'wordimage', //图片转存
              'lineheight', //行间距
              'edittip ', //编辑提示
              'customstyle', //自定义标题
              'autotypeset', //自动排版
              'touppercase', //字母大写
              'tolowercase', //字母小写
              'background', //背景
              'template', //模板
              'inserttable', //插入表格
            ]
          ]
        }
      }
    },
    props: {
      defaultMsg: {
        type: String
      },
      // config: {
      //   type: Object
      // }
    },
    watch: {
      'defaultMsg' (now, old) {
        let that = this;
        if (now) {
          setTimeout(() => {
            that.editor.setContent(now)
          }, 600)
        }
      }
    },
    mounted() {
      const _this = this
      // 自定义按钮
      window.UE.plugin.register('imagedes', function () {
        return {
          commands: {
            'imagedes': {
              execCommand: function () {
                // 调用图片管理
                _this.toUpLoad = true;
                _this.imgs = [];
                // _this.$refs.upload.toCheckPic()
              }
            }
          }
        }
      })
      this.editor = window.UE.getEditor(this.editorId, this.config) // 初始化UE

      this.editor.ready = function () {
        var msg = _this.defaultMsg
        _this.editor.setContent(msg) // 确保UE加载完成后，放入内容。
      }
      this.editor.addListener('blur', function () {
        var content = _this.editor.getContent()
        _this.$emit('content', content)
      })
      // setTimeout( () => {
      //   this.editor.setContent(this.defaultMsg)
      // },2000)
    },
    methods: {
      // 自定义上传接口
      upLoadImg(param) {
        const fileObj = param.file;
        // 接收文件上传地址
        const URL = '/upload/nofilter/file'
        // FormData 对象
        var param = new FormData();
        // 文件对象
        param.append("uploadFile", fileObj);
        param.append('type', '1')
        param.append('usageType', '3')
        // 其他参数
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let that = this;
        // 添加请求头
        axios.post(URL, param, config)
          .then(response => {
            if (response.data.result) {
              that.imgs.push({
                imgPath: response.data.data
              })
            } else {
              that.$message.error(response.data.msg || '上传失败！')
            }
          })
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        console.log()
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 10;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 10MB!');
        }
        return isJPG && isLt2M;
      },

      getUEContent() { // 获取内容方法
        return this.editor.getContent()
      },
      // 上传模块的图片选中
      recieveImg() {
        let that = this
        this.imgs.map(item => {
          that.editor.execCommand('insertimage', [{
            src: item.imgPath
          }])
        })

      },
      destroyed() {
        this.editor.destroy();
      }

    }

  }
</script>
<style>
  .edui-for-imagedes .edui-icon {
    background-image: url('upload-other.png') !important;
    background-repeat: no-repeat;
    background-position: 2px 5px;
  }

  .el-upload {
    width: 100%;
  }

  .el-upload-dragger {
    width: 100% !important;
  }
</style>


